*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body{
    font-family: Arial, Helvetica, sans-serif;
    background: #f4f4f4;
    height: 100%;
    width: 100%;
    color: #f4f4f4;
    font-size: 100%;
}

#root {
    position: relative;
    max-width: 80%;
    min-width: 300px;
    min-height: 500px;
    border: 5px solid #ddd;
    margin: 10% auto;
    padding: 15px 18px;
    border-radius: 15px;
    background: rgb(2, 2, 51);
}

.top-title {
    position: absolute;
    left: 50%;
    font-weight: 600;
    font-size: 20px;
    transform: translateX(-50%);
}

.top-title span {
    color: limegreen;
}

.top-bar {
    position: relative;
    display: flex;
    align-items: center;
}

.menu-bar h4{
    font-size: 10.5px;
    color: #ccc;
}
.hamburger {
    position: absolute;
}

.hamburger .line {
    width: 25px;
    height: 2px;
    background: #615f5f;
    margin: 3px 0px;
    cursor: pointer;
}

.hamburger:hover .line {
    background: #ccc;
}

.menu {
    position: absolute;
    left: 50%;
    top: 25%;
    width: 60%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 400px;
    display: none;
}

.menu-option {
    color: #bbb;
    background: linear-gradient(285deg, rgba(169, 167, 167, 0.1)50%, rgba(146, 145, 145, 0.18)50%);
    font-weight: 700;
    text-align: center;
    font-size: 17px;
    border-radius: 10px;
    padding: 3px;
    margin: 15px;
    cursor: pointer;
}

.showMenu {
    display: block;
}

.closeMenuBtn {
    color: darkred;
    font-weight: 800;
    text-align: center;
    cursor: pointer;
    
}
.closeMenuBtn:hover {
    color: red;
}

#sound {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3px 8px;
    gap: 20px;
}

/* Sound Toggle Design */

#sound .toggleSound {
    --width: 40px;
    --height: calc(var(--width)/2.5);
    --border-radius: calc(var(--height)/2);
    cursor: pointer;
    display: inline-block;
}

#sound .toggleSound-input {
    display: none;
}

#sound .toggleSound-fill {
    position: relative;
    width: var(--width);
    height: var(--height);
    border-radius: var(--border-radius);
    background: #bbb;
    color: #555;
    font-size: 8px;
    padding: 3px;
    display: flex;
    align-items: center;
    justify-content: right;
    transition: background 0.25s linear;

}

#sound .toggleSound-fill::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: var(--height);
    width:calc(var(--width)/2);
    border-radius: calc(var(--height)/2);
    background: #eee;
    box-shadow: 0 0 10px rgba(0,0,0,0.9);
    transition: transform 0.3s linear;

}

#sound .toggleSound-input:checked + .toggleSound-fill {
    background: #009758;
    justify-content: left;
    color: #ddd;
    font-size: 9px;
}

#sound .toggleSound-input:checked + .toggleSound-fill::after {
    transform: translateX(calc(var(--width)/2));
}

/* End of Sound Design Toggling */


.playBtnContainer {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #111;
    height: 60px;
    width: 60px;
    padding: 10px;
    border-radius: 50%;
    border: 2px solid green;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: roll 1.1s linear infinite;
    transform-origin: center;
    pointer-events: none;
}


.playBtn {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    color: #ccc;
}

.playBtn:hover {
    color: #fff;
}

